import React from 'react';

function Scores(props){
    const {score, highScore} = props

    return(
        <div className="scores">
            <div className="score">
                <h3>Current</h3>
                <h1>{score}</h1>
            </div>
            <div className="highScore">
                <h3>High</h3>
                <h1>{highScore}</h1>
            </div>
        </div>
    )
}

export default Scores;