import './App.css';
import React, { useState , useEffect} from 'react';


import Card from './components/Card';
import Scores from './components/Scores'

import Circle from './images/circle.svg'
import Clock from './images/clock.svg'
import Hexagon from './images/hexagon.svg'
import Water from './images/water.svg'
import Pig from './images/pig.svg'
import Rocket from './images/rocket.svg'
import Square from './images/square.svg'
import Triangle from './images/triangle.svg'

function App() {
  const [chosen, setChosen] = useState([])
  const [score, setScore] = useState(0)
  const [highScore, setHighScore] = useState(0)
  const cardDetails = [
    {name: 'circle', img: Circle},
    {name: 'clock', img: Clock},
    {name: 'hexagon', img: Hexagon},
    {name: 'water', img: Water},
    {name: 'pig', img: Pig},
    {name: 'rocket', img: Rocket},
    {name: 'square', img: Square},
    {name: 'triangle', img: Triangle},
  ];
  
  useEffect(() =>{
    document.title = 'Memory Game';
  }, []);
  

  function handleClick(e){
    e.preventDefault()
    setChosen(chosen.concat([e.target.id]))
    if(!chosen.includes(e.target.id)){
      setScore(score + 1)
      checkWin();
    }else{
      setChosen([])
      if(score > highScore){
        setHighScore(score)
        }
      setScore(0)
    }
  }

  function checkWin(){
    if(score === 8){
      //TODO
    }
  }

  function shuffle(array) {
    let currentIndex = array.length,  randomIndex;
  
    // While there remain elements to shuffle.
    while (currentIndex !== 0) {
  
      // Pick a remaining element.
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;
  
      // And swap it with the current element.
      [array[currentIndex], array[randomIndex]] = [
        array[randomIndex], array[currentIndex]];
    }
  
    return array;
  }

  function createCards(){
    let cards = [];
    cardDetails.forEach(card =>{
      cards.push(<Card name={card.name} id={card.name} img={card.img} onClick={handleClick}/>)
    })
    return cards; 
  }

  let cards = createCards()
  cards = shuffle(cards)

  return (
    <div className="App">
      <Scores score={score} highScore={highScore}/>
      <div className="cards">
        {cards}
      </div>
    </div>
  );
}

export default App;
